

.imgCard {
  width: 300px;
  height: 300px;
  border-radius: 1rem;
}

.flip-card {
    border-radius: 1rem;
  background-color: transparent;
  width: 300px;
  height: 300px;
  perspective: 1000px;
}

.flip-card-inner {
    border-radius: 1rem;
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card:hover .flip-card-inner {
    border-radius: 1rem;
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
    border-radius: 1rem;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
    border-radius: 1rem;
  background-color: #bbb;
  color: black;
}

.flip-card-back {
    border-radius: 1rem;
background-image: linear-gradient(205deg, #ff9162 0, #ff8464 12.5%, #ff7262 25%, #ff5a5e 37.5%, #ff3858 50%, #ef0053 62.5%, #e10052 75%, #d60055 87.5%, #ce005c 100%);
  color: white;
  transform: rotateY(180deg);
}

.dataContainer{
  margin: 10% 2%;
  display: flex;
  flex-direction: column;
}
